.sprite-svg{
    width: 30px;
    height: 30px;
}

.zag--black{
    font-weight: 300;
    font-size: 50px;
    line-height: 130%;
    text-align: center;
    width: 100%;
}
h1{
    font-weight: bold;
    font-size: 40px;
    color: #232323;
    line-height: 1.1;
}
.zag_main{
    text-align: center;
    margin-top: 20px;
    padding-bottom: 40px;
    margin-bottom: 40px;
    position: relative;
    &::before{
        content: '';
        width: 80%;
        height: 1px;
        left: 10%;
        bottom: 0px;
        position: absolute;
        background-color: #D8DFE2;
    }
    p{
        margin-top: 20px;
    }
}
strong, b{
    font-weight: 600;
}
.scrollup{
    position: fixed;
    bottom: 30px;
    right: 30px;
    border-radius: 50%;
    width: 54px;
    height: 54px;
    background: #FFFFFF;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
    z-index: 99;
    align-items: center;
    justify-content: center;
    display: flex;
    svg{
        transition: .3s;
        width: 22px;
        height: 22px;
        fill: #000000;
    }
    &:hover{
        svg{
            fill: $red;
        }
    }
}

.input-text-label{
    position: relative;
    label{
        position: absolute;
        left: 15px;
        pointer-events: none;
        top: 0;
        transition: .3s;
    }
    input[type='text']{
        transition: .3s;
        border-color:  #ACACAC;
    }
}
.input-text-label.focus{
    label{
        top: -14px;
        font-size: 12px;
        line-height: 120%;
        color: #ACACAC;
    }
    input[type='text']{
        border-color: #3A3A3A;
    }
}
.tabs-content{
    display: none;
}
.tabs-content.active{
    display: block;
}
.popup--video{
    position: relative;
    &::before{
        content: '';
        width: 82px;
        height: 82px;
        background-image: url(../images/svg/play.svg);
        position: absolute;
        left: calc(50% - 41px);
        top: calc(50% - 41px);
        background-size: contain;
        transition: .3s;
    }
    &:hover:before{
        transform: scale(1.3);
    }
}
.sbmt--red{
    padding: 12px 30px;
    box-shadow: none;
    border: 2px solid #EF4949;
    background: #EF4949;
    border-radius: 5px;
    color: #fff;
    transition: .3s;
    cursor: pointer;
    font-family: "montserat";
    &:hover{
        color: #EF4949;
        background: transparent;
    }
}
.btn--gray{
    padding: 12px 30px;
    box-shadow: none;
    border: 2px solid #546062;
    background: #fff;
    border-radius: 5px;
    color: #546062;;
    transition: .3s;
    cursor: pointer;
    font-family: "montserat";
    &:hover{
        color: #fff;
        background: #546062;
    }
}
.sbmt--red-border{
    padding: 12px 30px;
    box-shadow: none;
    border: 2px solid #EF4949;
    border-radius: 5px;
    color: #EF4949;
    background-color: transparent;
    transition: .3s;
    cursor: pointer;
    font-family: "montserat";
    &:hover{
        background-color: #EF4949;
        color: #fff;
    }
}

.page404{
    img{
        margin: 115px auto 50px;
        display: block;
    }
    &__text{
        display: flex;
        max-width: 990px;
        justify-content: space-between;
        margin: 0 auto 80px;
        align-items: center;
        p{
            font-weight: 600;
            font-size: 22px;
            line-height: 30px;
            color: #414141;
            width: calc(100% - 300px);
        }

}
}