// Импорт файлов
@import 'reset', 'mixin', 'fonts', 'sprite', 'color', 'all', '_popup';

@import 'block/header', 'block/main-top', 'block/plants-block', 'block/main-about', 'block/main-article', 'block/brands', 'block/form-border', 'block/article-item', 'block/footer', 'breadcrums';
@import 'block/catalog/main-catalog', 'block/catalog/catalog-filter', 'block/catalog/catalog-item', 'block/catalog/catalog-search', 'block/catalog/catalog-tabs', 'block/advantage-catalog', 'block/text/text', 'block/arrow', 'block/pagination', 'block/catalog/catalog-all', 'block/extra-block', 'block/card/card-info', 'block/card/card-tabs';

@import 'block/lc/lc', 'block/lc/lc-history', 'block/lc/lc-histrory-info', 'block/lc/lc-menu', 'block/lc/lc-tabs', 'block/lc/lc-program', 'block/lc/lc-profile', 'block/lc/lc-grafic';

@import 'block/checkout/checkout', 'block/basket/basket', 'block/selection', 'block/faq', 'block/contact', 'block/news', 'block/search', 'block/compare', 'block/company';

@import "block/brands-page", "block/form-opt", "block/certificate-gallery", "block/contact-juridical", "block/fixed-compare";


// Переменные
$font: "montserat";
$container: 1440px;
$main_color: #363636;


$grid-columns: 12;
@for $i from 1 through $grid-columns {
    .col-#{$i}{
        width: percentage($i / $grid-columns);
    }
}


*{
    box-sizing: border-box;
}
a{
    text-decoration: none;
    color: $main_color;
    transition: .2s;
    &:hover{
        color: $red;
    }
}
img{
    max-width: 100%;
}
article{
    width: 100%;
    margin: 0 auto;
    position: relative;
    min-height: calc(100vh - 400px);
}
// Стили
.container {
    max-width: $container;
    margin: 0 auto;
    width: 100%;
    padding: 0 20px;
}
.container--flex{
    display: flex;
    flex-wrap: wrap;
}
html{
    overflow: visible;
}
body {
    font-family: $font;
    font-weight: 400;
    overflow: visible;
    overflow-x: hidden;
    color: $main_color;
    font-size: 16px;
    line-height: 1.6em;
    letter-spacing: -0.02em;
}
.tabs__content {
    display: none; /* по умолчанию прячем все блоки */
}
.tabs__content.active {
    display: flex; /* по умолчанию показываем нужный блок */
}

@media screen and (max-width: 1650px){

}

@media screen and (max-width: 1460px){
    .container{
        max-width: 1190px;
    }
    
}

@media screen and (max-width: 1200px){
    .container{
        max-width: 990px;
    }
}

@media screen and (max-width: 990px){
    .container{
        padding: 0 20px;
    }
}

