.popup-form--main {
	.mfp-content {
		max-width: 600px;
		width: 100%;
	}
}

.popup-form {
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	background: #FFFFFF;
	overflow: hidden;
	&--main{
		.mfp-close{
			width: 25px;
			height: 25px;
			background-image: url(../images/svg/close-black.svg);
			background-size: contain;
			right: 20px !important;
			font-size: 0;
			top: 20px !important;
		}
	}
	&__error{
		display: none;
		width: 100%;
		font-size: 16px;
		color: #36393A;
		line-height: 120%;
		align-items: center;
		justify-content: center;
		padding: 15px;
		background-color: #D8E3E5;
		svg{
			fill: #FFA34E;
			margin: 0 10px;
		}
	}
	&__error.error{
		display: flex;
	}

	&__zag {
		min-height: 121px;
		padding: 10px;
		text-align: center;
		font-weight: 600;
		font-size: 30px;
		line-height: 120%;
		color: #414141;
		background: #E9F1F3;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	&__hello {
		padding: 35px 70px;

		div {
			padding: 10px 10px 10px 60px;
			font-size: 14px;
			line-height: 1.6;
			color: #333131;
			a{
				color: $red;
				text-decoration: underline;
				&:hover{
					text-decoration: none;
				}
			}
		}
	}

	input[type='text'],
	input[type='password'],
	input[type='email']{
		padding: 10px 15px;
		border: 0;
		border-bottom: 1px solid #ACACAC;
		max-width: 100%;
		width: 100%;
		transition: .3s;
		margin-right: 0px;
		&:focus {
			border-color: #767676;
		}

	}

	input::placeholder {
		font-size: 16px;
		line-height: 120%;
		color: #000000;
		opacity: .8;
		font-family: "montserat";
	}

	input[type='submit'] {
		padding: 12px 30px;
		box-shadow: none;
		border: 2px solid #EF4949;
		border-radius: 5px;
		color: #EF4949;
		background-color: transparent;
		transition: .3s;
		cursor: pointer;
		font-family: "montserat";

		&:hover {
			background-color: #EF4949;
			color: #fff;
		}
	}

	.input-text-label {
		margin-bottom: 30px;
		width: 100%;
	}

}

.tabs-nav {
	padding: 40px 70px 0;
	display: flex;
	align-items: center;
	justify-content: center;

	li {
		width: 50%;
		cursor: pointer;
		text-align: center;
		padding: 15px 5px;
		font-weight: 600;
		font-size: 14px;
		line-height: 135%;
		color: #2B2D2D;
		border: 1px solid #AAB2B2;
		margin-right: -1px;
		z-index: 1;
		transition: .3s;
		text-transform: uppercase;
		position: relative;
		&:first-child{
			border-top-left-radius: 5px;
			border-bottom-left-radius: 5px;

		}
		&:last-child{
			border-top-right-radius: 5px;
			border-bottom-right-radius: 5px;

		}
		&::before {
			content: '';
			@include triangle(12, 7, #F74B4B, top);
			position: absolute;
			left: calc(50% - 6px);
			bottom: -1px;
			opacity: 0;
			transition: .3s;

		}
	}

	.active {
		color: #F74B4B;
		z-index: 2;
		border: 1px solid #4D5151;

		&::before {
			opacity: 1;
		}
	}
}

.tabs-content {
	padding: 40px 70px 0;
}

.input-checkbox {
	padding-left: 40px;

	label {
		font-size: 14px;
		line-height: 22px;
		color: #333131;
	}

	.jq-checkbox {
		margin-left: -40px;
		margin-right: 20px;
	}
}

.jq-checkbox {
	background: none;
	border: 1px solid #636367;
	box-sizing: border-box;
	border-radius: 2px;
	width: 20px;
	height: 20px;
	box-shadow: none;
	outline: none;

}
.jq-checkbox.error{
	border-color: #FF784E;
}

.jq-checkbox.checked .jq-checkbox__div {
	width: 12px;
	height: 8px;
	border-bottom: 2px solid  #fff;
	border-left: 2px solid  #fff;
}
.input-checkbox .jq-checkbox.checked{
	background-color: #EF4949;
	border-color: #EF4949;
}
.jq-checkbox.focused{
	border-color: #EF4949;
}
.input-bottom {
	padding: 50px 0px 30px;
	margin-top: 50px;
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	justify-content: space-between;
	align-items: center;
	position: relative;
	&::before{
		content: '';
		width: calc(100% + (70px * 2));
		position: absolute;
		left: -70px;
		height: 1px;
		background-color: #D8DFE2;
		top: 0;
	}
	span {
		font-weight: 500;
		font-size: 14px;
		line-height: 135%;
	}

	.help {
		margin-top: 24px;
		text-align: center;
		width: 100%;

		a {
			font-size: 14px;
			line-height: 22px;
			text-decoration-line: underline;
			color: #333131;

			&:hover {
				text-decoration: none;
			}
		}
	}

	.help.w50 {
		width: auto;
		margin-top: 0;
	}

	button {
		padding: 12px 30px;
		box-shadow: none;
		border: 2px solid #EF4949;
		border-radius: 5px;
		color: #EF4949;
		background-color: transparent;
		transition: .3s;
		cursor: pointer;
		font-family: "montserat";

		&:hover {
			background-color: #EF4949;
			color: #fff;
		}
	}
	.button {
		padding: 12px 30px;
		box-shadow: none;
		border: 2px solid #EF4949;
		border-radius: 5px;
		color: #EF4949;
		background-color: transparent;
		transition: .3s;
		cursor: pointer;
		font-family: "montserat";

		&:hover {
			background-color: #EF4949;
			color: #fff;
		}
	}
}

.error-block{
	display: none;
	position: absolute;
	right: 0;
	top: -8px;
	z-index: 10;
	width: 30px;
	height: 30px;
	&:hover{
		z-index: 99;
		.error-block__message{
			display: block;
		}
	}
	&__ico{
		z-index: -1;
		position: absolute;
		display: flex;
		top: 8px;
		right: 0;
		align-items: center;
		justify-content: center;
		svg{
			fill: #FFA34E;
			width: 25px;
			height: 25px;
		}

	}
	&__message{
		z-index: 799;
		display: none;
		right: -5px;
		top: calc(100% + 10px);
		position: absolute;
		padding: 7px 10px;
		width: 165px;
		font-size: 14px;
		line-height: 114%;
		color: #303030;
		background: #EDF2F2;
		border-radius: 2px;
		&::before{
			content: '';
			@include triangle(12,6,#EDF2F2,top);
			position: absolute;
			right: 12px;
			top: -6px;
		}
	}
}

.input-text-label.error{
	input[type='text'],
	input[type='password'],
	input[type='email']{
		border-color: #FF784E;
	}
	.error-block{
		display: block;
	}
}

.input-text-label.error-online{
	input[type='text'],
	input[type='password'],
	input[type='email']{
		border-color: #FF784E;
	}
	.error-block{
		display: block;
	}
}
@media screen and(max-width: 600px){
	.popup-form__zag{
		padding: 5px 30px;
		min-height: 70px;
		font-size: 18px;
	}
	.popup-form--main .mfp-close {
		width: 20px;
		height: 20px;
		right: 7px !important;
		top: 7px !important;
	}
	.tabs-nav {
		padding: 20px 20px 0;
	}
	.tabs-content {
		padding: 20px 20px 0;
	}
	.popup-form .input-text-label {
		margin-bottom: 25px;
	}
	.input-bottom {
		padding: 20px 0px;
		margin-top: 20px;
	}
	.tabs-nav li{
		font-size: 10px;
		padding: 10px 5px;
	}
	.input-checkbox {
		padding-left: 30px;
	}
	.input-checkbox .jq-checkbox {
		margin-left: -30px;
		margin-right: 10px;
	}
	.input-checkbox label{
		font-size: 12px;
		br{
			display: none;
		}
	}
	.input-text-label.focus label{
		top: -10px;
	}
	.popup-form input[type='submit'],
	.input-bottom button{
		padding: 7px 10px;
		font-size: 12px;
	}
	.input-bottom span {
		font-size: 10px;
	}
	.input-bottom .help {
		margin-top: 15px;
	}
	.input-text-label label{
		font-size: 12px;
	}
	.input-bottom .help.w50{
		width: 100%;
		text-align: left;
		margin-top: 15px;
	}
	.popup-form__hello{
		padding: 20px;
	}
	.popup-form__hello div {
		padding: 0px 0px 0px 45px;
		background-size: 30px;
		font-size: 12px;
	}
	.popup-form__error{
		font-size: 10px;
		padding: 10px 5px;
	}
	.popup-form__error svg {
		width: 20px;
		height: 20px;
		margin: 0 5px;
	}
	.error-block__message{
		font-size: 10px;
	}
}